import React, {useEffect, useState} from "react";
import { Navigation, Pagination } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';

export const Prices = () => {


    const [swiperInitialized, setSwiperInitialized] = useState(false);

    useEffect(() => {
        const initializeSwiper = () => {
            //  Swiper    768px  
            if (window.innerWidth < 768 && !swiperInitialized) {
                setSwiperInitialized(true);
            }
        };

        //     
        initializeSwiper();

        //           
        window.addEventListener('resize', initializeSwiper);

        //     
        return () => {
            window.removeEventListener('resize', initializeSwiper);
        };
    }, [swiperInitialized]);


    return (  swiperInitialized ?
        <div className="prices">
            <div className="title-block">  </div>

            <Swiper
                modules={[Navigation, Pagination]}
                className="swiper"
                spaceBetween={16}
                slidesPerView="auto"
                initialSlide={0}
                pagination={{ clickable: true }}
                onSlideChange={() => console.log('slide change')}
                onSwiper={(swiper) => console.log(swiper)}
            >

                <SwiperSlide className="prices__slide swiper-slide">
                    <div className="prices__info">
                        <p className="title"> </p>
                        <p className="description">    </p>
                    </div>
                    <div className="prices__info">
                        <p className="title"></p>
                        <p className="description"></p>
                    </div>
                    <div className="prices__info">
                        <p className="title"></p>
                        <p className="description">30-120 </p>
                    </div>
                    <a className="button" href="#"></a>
                </SwiperSlide>

                <SwiperSlide className="prices__slide swiper-slide">
                    <div className="prices__info">
                        <p className="title"> </p>
                        <p className="description">    </p>
                    </div>
                    <div className="prices__info">
                        <p className="title"></p>
                        <p className="description"></p>
                    </div>
                    <div className="prices__info">
                        <p className="title"></p>
                        <p className="description">30-120 </p>
                    </div>
                    <a className="button" href="#"></a>
                </SwiperSlide>

                <SwiperSlide className="prices__slide swiper-slide">
                    <div className="prices__info">
                        <p className="title"> </p>
                        <p className="description">    </p>
                    </div>
                    <div className="prices__info">
                        <p className="title"></p>
                        <p className="description"></p>
                    </div>
                    <div className="prices__info">
                        <p className="title"></p>
                        <p className="description">30-120 </p>
                    </div>
                    <a className="button" href="#"></a>
                </SwiperSlide>

                <SwiperSlide className="prices__slide swiper-slide">
                    <div className="prices__info">
                        <p className="title"> </p>
                        <p className="description">    </p>
                    </div>
                    <div className="prices__info">
                        <p className="title"></p>
                        <p className="description"></p>
                    </div>
                    <div className="prices__info">
                        <p className="title"></p>
                        <p className="description">30-120 </p>
                    </div>
                    <a className="button" href="#"></a>
                </SwiperSlide>

                <SwiperSlide className="prices__slide swiper-slide">
                    <div className="prices__info">
                        <p className="title"> </p>
                        <p className="description">    </p>
                    </div>
                    <div className="prices__info">
                        <p className="title"></p>
                        <p className="description"></p>
                    </div>
                    <div className="prices__info">
                        <p className="title"></p>
                        <p className="description">30-120 </p>
                    </div>
                    <a className="button" href="#"></a>
                </SwiperSlide>

            </Swiper>

            <p className="prices__bottom-info">     .      .       ,  .</p>
            <div className="prices__bottom-link"><a href="#" >  </a></div>
        </div>
            :
        <div className="prices">
            <div className="title-block">  </div>
            <div className="rows">
                <div className="rows__head">
                    <div className="row__item"><p> </p></div>
                    <div className="row__item"><p></p></div>
                    <div className="row__item"><p></p></div>
                    <div className="row__item"></div>
                </div>
                <div className="row">
                    <div className="row__item"><p></p></div>
                    <div className="row__item"><p></p></div>
                    <div className="row__item"><p>30 </p></div>
                    <div className="row__item"><a className="button" href="#"></a></div>
                </div>
                <div className="row">
                    <div className="row__item"><p> </p></div>
                    <div className="row__item"><p>1 000 </p></div>
                    <div className="row__item"><p>30-120 </p></div>
                    <div className="row__item"><a className="button" href="#"></a></div>
                </div>
                <div className="row">
                    <div className="row__item"><p>  </p></div>
                    <div className="row__item"><p>1 000 </p></div>
                    <div className="row__item"><p>30-120 </p></div>
                    <div className="row__item"><a className="button" href="#"></a></div>
                </div>
                <div className="row">
                    <div className="row__item"><p>    </p></div>
                    <div className="row__item"><p>1 000 </p></div>
                    <div className="row__item"><p>30-120 </p></div>
                    <div className="row__item"><a className="button" href="#"></a></div>
                </div>
                <div className="row">
                    <div className="row__item"><p>  </p></div>
                    <div className="row__item"><p>1 000 </p></div>
                    <div className="row__item"><p>30-120 </p></div>
                    <div className="row__item"><a className="button" href="#"></a></div>
                </div>
            </div>

            <p className="prices__bottom-info">     .      .       ,  .</p>
            <div className="prices__bottom-link"><a href="#" >  </a></div>
        </div>
    );
}
